/* Base */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  html {
    scroll-behavior: smooth;
  }
  
  body,
  button,
  input,
  textarea {
    font-family: var(--body-font);
    font-size: var(--normal-font-size);
  }
  
  body {
    background-color: var(--body-color);
    line-height: 1.7;
    color: var(--text-color);
  }
  
  h1, h2, h3 {
    color: var(--title-color);
    font-weight: var(--font-semibold);
    line-height: 1.2;
  }
  
  ul {
    list-style: none;
  }
  
  a {
    text-decoration: none;
  }
  
  button {
    cursor: pointer;
    border: none;
    outline: none;
  }
  
  img {
    max-width: 100%;
    height: auto;
    vertical-align: middle;
  }
  
  /* Resuable CSS Classes */
  .container {
    max-width: 1080px;
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;
  }
  
  .grid {
    display: grid;
  }
  
  .section {
    padding-top: 7rem;
    padding-bottom: 2rem;
  }
  
  .section__title {
    font-size: var(--h1-font-size);
    margin-left: 0.875rem;
    font-weight: var(--font-bold);
    position: relative;
    margin-bottom: 3.75rem;
  }
  
  .section__title::before {
    content: '';
    background: url(../dots-bg.svg);
    height: 2.25rem;
    width: 2.25rem;
    position: absolute;
    left: -0.875rem;
    top: -0.875rem;
  }
  
  .btn {
    padding: 0.75rem 2rem;
    line-height: 1;
    border-radius: 1.875rem;
    box-shadow: 0 0 1px rgb(0 0 0 / 0%);
    color: #fff;
    display: inline-block;
    background-color: var(--first-color);
    font-weight: var(--font-bold);
  }
  
  @keyframes button-push {
    50% {
      transform: scale(0.8);
    }
  
    100% {
      transform: scale(1);
    }
  }
  
  .btn:hover {
    animation: button-push 0.3s linear 1;
  }

  .main {
    margin-left: 110px;
  }
  
  @media screen and (max-width: 1024px) {
    .main {
      margin-left: 0;
    }
  }

/* Beranda */
  
  .beranda {
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.intro {
    max-width: 540px;
    text-align: center;
}

.beranda__img {
    margin-bottom: 1.5rem;
    width: 150px;
}

.beranda__nama {
    font-size: var(--h1-font-size);
    font-weight: var(--font-bold);
    margin-bottom: 0.5rem;
}

.beranda__sosial {
    display: flex;
    column-gap: 1.75rem;
    margin: 1.5rem 0;
    justify-content: center;
}

.beranda__sosial-link {
    color: var(--tittle-color);
    font-size: 1.3rem;
    transition: .3s;
}

.beranda__sosial_link:hover {
    color: hsl(43, 100%, 68%)
}

.beranda__edukasi {

}

.scroll {
    position: absolute;
    bottom: 2.5rem;
    left: 0;
    width: 100%;
}

.beranda__scroll_nama {
    font-size: var(--small-font-size);
}

.mouse {
    border: 2px solid var(--text-color);
    display: block;
    height: 1.6rem;
    width: 1.25rem;
    margin: auto;
    margin-top: .75rem;
    border-radius: 1rem;
    position: relative;
}

.mouse__wrapper {

}

@keyframes ani-mouse {
    0% {
        top: 29%;
    }
    15% {
        top: 50%;
    }
    50% {
        top: 50%;
    }
    100% {
        top: 29%;
    }
}

.wheel {
    background-color: var(--title-color);
    border-radius: 100%;
    width: 0.25rem;
    height: 0.25rem;
    position: absolute;
    top: .5rem;
    left: 50%;
    transform: translateX(-50%);
    animation: ani-mouse 2s linear infinite;
}

@media screen and (max-width: 1024px) {
    .beranda__sosial_link {
        font-size: 1.125rem;
    }
}

.btn {
  padding: 0.75rem 2rem;
  line-height: 1;
  border-radius: 1.875rem;
  box-shadow: 0 0 1px rgb(0 0 0 / 0%);
  color: #fff;
  display: inline-block;
  background-color: var(--first-color);
  font-weight: var(--font-bold);
}

@keyframes button-push {
  50% {
    transform: scale(0.8);
  }

  100% {
    transform: scale(1);
  }
}

.btn:hover {
  animation: button-push 0.3s linear 1;
}

/* Tentang */

.sidebar {
    position: fixed;
    left: 0;
    top: 0;
    background: var(--body-color);
    border-right: 1px solid rgba(0, 0, 0, 0.05);
    padding: 2.5rem;
    width: 110px;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    z-index: 10;
    transition: .3s;
}

.nav__logo {
    width: 30px;
}

.nav__menu {

}

.nav__list {
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
}

.nav__item {

}

.nav__link {
    font-size: 1.5rem;
    color: var(--title-color);
    font-weight: var(--font-bold);
    transition: .3s;
}

.nav__link:hover {
    color: hsl(43, 100%, 68%);
}

.nav__footer {

}

.copyright {
    color: hsl(245, 15%, 65%);
    font-size: var(--small-font-size);
    transform: rotate(-180deg);
    writing-mode: vertical-rl;
}

.nav__toggle {
    position: fixed;
    top: 1.25rem;
    left: 1.875rem;
    cursor: pointer;
    height: 40px;
    width: 45px;
    background-color: var(--body-color);
    border: 1px solid #e8dfec;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    transition: .3s;
}

@media screen and (max-width: 1024px) {
    .sidebar {
      left: -110px;
    }

    .nav__toggle {
        display: flex;
    }

    .nav__toggle__open {
        left: 140px;
    }

    .show__menu {
        left: 0;
    }
}

/* Tentang Saya */

.tentang {

}

.tentang__container {
    grid-template-columns: 3fr 9fr;
    column-gap: 1.875rem;
}

.tentang__data {
    padding: 1.875rem;
    background-color: var(--container-color);
    box-shadow: var(--shadow);
    border-radius: var(--border-radius);
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1.875rem;
    align-items: flex-start;
    position: relative;
}

.tentang__data::before {
    content: '';
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-right: 15px solid var(--container-color);
    position: absolute;
    left: -0.93rem;
    top: 20%;
}

.tentang__description {
    margin-bottom: 1rem;
    text-align: justify;
}

.tentang__skills {
    row-gap: 1.25rem;
}

.skills__title {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;
}

.skills__name {
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
}

.skills__number {
    line-height: 1.2;
}

.skills__bar,
.skills__percentage {
    height: 7px;
    border-radius: 0.25rem;
}

.skills__bar {
    background-color: #f1f1f1;
}

.skills__percentage {
    display: block;
}

.development {
    width: 80%;
    background-color: rgb(255, 209, 92);
}

.javascript {
    width: 60%; 
    background-color: rgb(255, 209, 92);
}

.php {
    width: 70%; 
    background-color: rgb(255, 209, 92);
}

.ci {
    width: 60%; 
    background-color: rgb(255, 209, 92);
}

.mysql {
    width: 70%; 
    background-color: rgb(255, 209, 92);
}

.tentang__boxes {
    grid-template-columns: repeat(3, 1fr);
    column-gap: 1.875rem;
    margin-top: 4.35rem;
}

.tentang__box {
    display: flex;
    column-gap: 1.5rem;
}

.tentang__icon {
    font-size: var(--h1-font-size);
    color: #dedeea;
}

.tentang__title {
    font-size: 1.875rem;
}

@media screen and (max-width: 1024px) {
    .tentang__container {
        grid-template-columns: 720px;
        justify-content: center;
        row-gap: 1.875rem;
    }

    .tentang__data::before {
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-bottom: 10px solid #fff;
        left: 49%;
        top: -20px;
    }

    .tentang__img {
        justify-self: center;
    }

    .tentang__box {
        flex-direction: column;
        row-gap: .5rem;
        text-align: center;
    }
}

@media screen and (max-width: 768px) {
    .tentang__container {
        grid-template-columns: 350px;
    }

    .tentang__data::before {
        left: 47%;
    }

    .tentang__data {
        grid-template-columns: 1fr;
        row-gap: 1.875rem;
    }

    .tentang__boxes {
        grid-template-columns: repeat(2, 150px);
        row-gap: 1.5rem;
        justify-content: center;
    }
}

@media screen and (max-width: 576px) {
    .tentang__container {
        grid-template-columns: 320px;
    }
}

@media screen and (max-width: 350px) {
    .tentang__container {
        grid-template-columns: 1fr;
    }

    .tentang__boxes {
        grid-template-columns: 1fr;
    }
}